.registration_container {
    @apply gap-50 mb-100 flex flex-col items-center;
    h1 {
        @apply max-w-328 mx-auto text-center text-black;
        @media (min-width: 768px) {
            @apply max-w-704;
        }
        @media (min-width: 1024px) {
            @apply max-w-904;
        }
    }
}